.movie {
    
    display:flex;
    align-items: flex-start;
    
    width:calc(50% - 15px);
    padding: 20px;
    margin-bottom: 50px;
    box-sizing: border-box;

    background:#fff;
    box-shadow: 2px 2px 12px rgba(0,0,0,0.04);
}

.movie img {
    transform: translateY(-40px);
    width:100px;
    box-shadow: 2px 2px 8px rgba(0,0,0,0.4);
    transition: 0.3s;
}

.movie img:hover {
    box-shadow: 4px 4px 20px rgba(6, 4, 4, 0.3);
    transform: translateY(-40px) scale(1.01);
}

.movie_data {
    padding-left:20px;
}

.movie_title {
    font-size:20px;
    line-height: 24px;
    margin:0 0 10px;
}
.movie_year {
    margin:0;
    font-size:16px;
    color:#6AC045;
}
.movie_genres {
    display:flex;
    flex-wrap: wrap;
}

.movie_genres li {
    margin-right:5px;
    padding:1px 4px;
    background:#333;
    color:#fff;
    margin-bottom:5px;
}