@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

body {
    background:#f9f9f9;
    font-family: 'Poppins', sans-serif;
    color:#333;
    font-size:12px;
    font-weight:400;
}

ul, li {
    list-style: none;
    margin:0;
    padding:0;
}

/* loading */
.load_img {
    position:fixed;
    left:50%;
    top:50%;
    transform: translate(-50% -50%);
}

.header {
    position:fixed;
    left:0;
    top:0;
    background:#333;
    width:180px;
    height:100vh;
}

.header h1 {
    text-align: center;
    padding:15px 0;
}

nav.gnb {
    position:absolute;
    top:100px;
    z-index:10;
    padding:0 20px;
}

nav.gnb a {
    color:#fff;
    text-decoration: none;
    display: block;
    font-size:16px;
    line-height:28px;
}

@media screen and (max-width:767px){
    .header {
        position:static;
        width:100%;
        height:auto;
    }
}