/* main */

main {
    position:absolute;
    width:calc(100% - 180px);
    left:180px;
    top:0;
    padding:50px 30px;
    box-sizing:border-box;
}

main section {
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

@media screen and (max-width:767px){
    main {
        position:static;
        width:100%;
        left:0;
    }
}